
.container {
  width: 607px;
  height: 829px;
  margin: auto;
  margin-top: 75px;
  border-radius: 14px;
  background-image: linear-gradient(
    180deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(75, 14, 47, 1) 25%,
    rgba(0, 212, 255, 1) 100%
  );
}
.top-bar {
  display: flex;
  justify-content: center;
  gap: 14px;
  padding-top: 60px;
}
.top-bar input {
  display: flex;
  width: 352px;
  height: 78px;
  background-color: #ebfffc;
  border: none;
  outline: none;
  border-radius: 40px;
  padding-left: 40px;
  color: #626262;
  font-size: 20px;
  font-weight: 400;
}
.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 78px;
  height: 78px;
  background: #ebfffc;
  border-radius: 40px;
  cursor: pointer;
}
.weather-image {
  margin-top: 29px;
  display: flex;
  justify-content: center;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color:white;
    font-size:120px;
    font-weight:400px;
}
.weather-location{
    display: flex;
    justify-content: center;
    color:white;
    font-size:60px;
    font-weight:400px;
}
.data-container{
    display:flex;
    justify-content: center;
    margin-top : 50px;
    color: white;
}
.element{
    margin:auto; 
    display: flex;
    align-items: flex-start;
    gap:12px;
}
.data{
    font-size: 34px;
    font-weight: 400;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top:10px;
}
.errorMessage{
  display: flex;
  justify-content: center;
  align-items: center;
  margin:200px 60px 60px 60px;
  color:white;
  font-size: 25px;
  font-weight: 400;
  height: 78px;
  background-color : red;
  border: none;
  outline: none;
  border-radius: 35px;
  animation: handleError 3s ease-in infinite;
}
@keyframes handleError{
  from{
    background-color: pink;
    color:white
  }
  to{
    background-color: white;
    border:1px solid blue;
    color:red;
  }
}

